@for $i from 1 through 31 {
  .calendar div:nth-child(#{$i})::after {
    content: "#{$i}";
  }
}

@each $day, $index, $marginLeft in $days {
  .calendar-#{$day} {
    div:nth-child(1) {
      margin-left: #{$marginLeft + $index};
    }
  }
}
